<template>
  <div class="flip_a_coin">
    {{data}}
  </div>
</template>

<script>
  export default {
    name: "FlipCoin",
    data () {
      return {
        data: '',
      }
    },
    created() {
      if (typeof window.ethereum !== 'undefined') {
        this.data = "!!!!!!!!!!!"
      }
    }
  }
</script>

<style scoped>

</style>



